import React, { Component } from 'react';
import SpacetimeWatch from './spacetime-watch';
import logo from './logo.svg';
import './App.css';
import profile from './profile.json'
import devSkin from './dev-skin.json'

// profile
// - id:     to give a unique <symbol> id per instance
// - canvas: canvas area in pixels
// - view:   SVG viewport (coordinates ranges)
// This syntax works: ... style={{display:`${this.state.device_display}`}} />

const ProfileContent = ({count}) => {
  // console.log("skin switch count: " + count);
  // console.log(JSON.stringify(devSkin));
  // May.22,2018 refactored elegantly to avoid repeating codes
  // returns an array of React elements
  const lns = profile.map((pf, i) => { return (
      <div key={i} className={pf.device}>
        <img src={pf.imgSrc} alt={pf.device}  style={devSkin[count % devSkin.length].display} />
        <SpacetimeWatch id={i} profile={pf} bgColor={devSkin[count % devSkin.length].bgColor} />
      </div>
    );
  });

  return <div className="App-content">{lns}</div>;
}

class HeaderWatch extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date()
    };
  }

  componentDidMount() {
    this.timerID = setInterval(() => this.tick(), 1000);
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  tick() {
    this.setState(prevState => ({date: new Date()}));
  }

  render() {
    return <h1 className="App-title">{this.props.children} - {this.state.date.toLocaleTimeString()}</h1>;
  }
}

class App extends Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
    this.state = {
      count: 0   // to toggle skin images and other options
    };
  }
  
  handleClick() {
    this.setState(prevState => ({ count: prevState.count + 1 }));
  }

  render() {

    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <HeaderWatch>p0511 Spacetime Navigator</HeaderWatch>
          <button onClick={this.handleClick}>Switch Device Skin</button>
        </header>
        <svg>
          <filter id="blursun">
            <feGaussianBlur in="SourceGraphic" stdDeviation="2" result="blur5" />
            <feComposite in="SourceGraphic" in2="blur5" operator="arithmetic" k1="0" k2="3" k3="3" k4="0" />
          </filter>
        </svg>
        <ProfileContent count={this.state.count} />
      </div>
    );
  }
}

export default App;

/*
  operator="arithmetic"
  result = k1*in*in2 + k2*in + k3*in2 + k4

        <filter id="lighton">
          <feGaussianBlur stdDeviation="3" result="blur4" />
          <feSpecularLighting result="spec4" in="blur4" specularExponent="35" lightingColor="#cccccc">
            <fePointLight x={pSun.x} cy={pSun.y} z="800" />
          </feSpecularLighting>
          <feComposite in="SourceGraphic" in2="spec4" operator="arithmetic" k1="0" k2="1" k3="1" k4="0" />
        </filter>
*/



