// Soomin K., May 28, 2018
// SVG utility functions that will greatly enhance the readability of the source code
// These functions will also generate compact SVG codes by reducing unnecessary precision details from numeric data.
  import React from 'react';   // 'React' must be in scope when using JSX

// constants for readability
const C = {
// arc flags
  nxr:   0,    // no x-axis rotation
  larc:  1,    // large arc
  sarc:  0,    // small arc
  cw:    1,    // clockwise flag
  ccw:   0,    // counter-clockwise flag
};

// Deg to radian, to fill fully when deg==360
export const radDeg = (deg) => ((deg >= 360) ? 359.99 : deg)*Math.PI/180;
// large arc flag
export const largeArcFlag = (deg) => (deg >= 180) ? 1 : 0;

// Number.prototype.toFixed() returns a string.
// parseFloat() truncates trailing zeros
export const vFix = (v, p=4) => Number.isInteger(v) ? v : parseFloat(v.toFixed(p));
export const d_p = (x, y) => vFix(x) + ' ' + vFix(y);

// Cmd: command such as 'M', 'L'
const d_cmdp = (cmd, x, y) => cmd + d_p(x,y);
export const d_Mp = (x, y) => d_cmdp('M', x,y);
export const d_Lp = (x, y) => d_cmdp('L', x,y);
export const d_mp = (x, y) => d_cmdp('m', x,y);
export const d_lp = (x, y) => d_cmdp('l', x,y);

const d_cmdv = (cmd, v) => cmd + vFix(v);
export const d_Hx = (x) => d_cmdv('H', x);
export const d_hx = (x) => d_cmdv('h', x);
export const d_Vy = (y) => d_cmdv('V', y);
export const d_vy = (y) => d_cmdv('v', y);

// Move to (x,y) and then Line to (x2,y2)
export const d_MLp = (x, y, x2, y2) => d_Mp(x,y) + d_Lp(x2,y2);
export const d_Mlp = (x, y, x2, y2) => d_Mp(x,y) + d_lp(x2,y2);
// Move to (x,y) and then Horizontal Line to x=x2
export const d_MHx = (x, y, x2)     => d_Mp(x,y) + d_Hx(x2);
export const d_Mhx = (x, y, x2)     => d_Mp(x,y) + d_hx(x2);
export const d_MVy = (x, y, y2)     => d_Mp(x,y) + d_Vy(y2);
export const d_Mvy = (x, y, y2)     => d_Mp(x,y) + d_vy(y2);

// radius x, y
// x-axis-rotation: of the ellipse
// large-arc-flag: 0 if the arc's measure < 180 degree
// sweep-flag: 0 if negative angle direction (counter-clockwise)
// endinge x, y
export const d_Ap = (rx, ry, xar, laf, sf, x, y)   => d_cmdp('A', rx,ry) + ` ${xar} ${laf} ${sf} ` + d_p(x,y);
export const d_ap = (rx, ry, xar, laf, sf, dx, dy) => d_cmdp('a', rx,ry) + ` ${xar} ${laf} ${sf} ` + d_p(dx,dy);

// Circular (rx==ry) Arc Clockwise (default)
export const d_CACp  = (r, laf, x,  y,  sf=C.cw) => d_Ap(r, r, C.nxr,laf,sf,  x,  y);
export const d_CaCp  = (r, laf, dx, dy, sf=C.cw) => d_ap(r, r, C.nxr,laf,sf,  dx, dy);

// angle, instead of end point
export const d_CAC = (r, deg, sf=C.cw) => {
  const a = radDeg(deg);
  return d_CACp(r, largeArcFlag(deg), r*Math.sin(a), -r*Math.cos(a), sf);
}

// Circular (same x, y radius) arc with the given angle
// deg starts from 12 o'clock, default clockwise
// Use <path d={d_MCAC(...)} transform={"rotate(offsetDeg)"} />
export const d_MCAC = (r, deg, sf=C.cw) => d_Mp(0,-r) + d_CAC(r, deg, sf);

// folding fan with an angle of 2*deg from (0,0), y-axis symmetric
export const d_DoubleMCAC = (r, deg) => {
  const a  = radDeg(deg);
  const x1 =  r*Math.sin(a);
  const y  = -r*Math.cos(a);
  return d_MLp(0,0, -x1,y) + d_CACp(r, largeArcFlag(deg), x1, y) + 'Z';
}

// circle in <path> format, at the current location: clockwise
export const d_aCircle = (r) =>
  d_CaCp(r, C.larc,  r*2,0) +
  d_CaCp(r, C.larc, -r*2,0) + `z`;

// full circle, at first Move to the one end point in the circle
export const d_MaCircle  = (r, cx=0, cy=0) => d_Mp(cx-r,cy) + d_aCircle(r);
// Upper circle clockwise
export const d_MaUpperC  = (r, cx=0, cy=0) => d_Mp(cx-r,cy) + d_CaCp(r, C.larc, r*2,0);
// Lower circle counter-clockwise (to write text on)
export const d_MaLowerC  = (r, cx=0, cy=0) => d_Mp(cx-r,cy) + d_CaCp(r, C.larc, r*2,0, C.ccw);

// Writing on the Upper Circle begins from 9 o'clock clockwise
// and on the Lower Circle starts also from 9 o'clock and counterclockwise
// To get the position on the Arc, we need a coordinate conversion function
// i: clockwise index that begins from 0
// N: N sections in the full circle (ex, 12 for hours, 60 for minutes)
// ul: 0 for Upper, 1 for Lower circle
// ia: index in the (Upper or Lower) Arc
export const getArcId = (i, N) =>
  (i < N/4 || i > (N*3/4-1)) ?
    {ul: 0, ia: (i+N/4)%N}   :
    {ul: 1, ia: (N*3/4-1)-i};

// offset in percentage in the Arc
// N is the total number of divides in full circle
//`{(180/N + 360*ia/N)*100/180}%`
// i0=0 to set the initial position=0
export const offsetHArcP = (ia, N, i0=1) => vFix((i0+2*ia)*100/N) + '%';
export const offsetFArcP = (ia, N, i0=.5) => {
  let p = 25+(i0+ia)*100/N;
  if (p > 100)
    p -= Math.trunc(p/100)*100;  // to wrap the offset
  return vFix(p) + '%';
}

// closed path of the donut with the outer radius r and inner radius ri, and degree:
// Center should be at (0,0), otherwise transform:translate to the center.
export const d_Donut = (r, ri, deg) => {
  const a     = radDeg(deg);    // to fill fully when deg==360
  const sin_a = Math.sin(a);
  const cos_a = Math.cos(a);
  const laf   = largeArcFlag(deg);  // large-arc-flag
  return d_MVy(0,-ri,   -r)        + d_CACp(r,  laf, r*sin_a, -r*cos_a)   +
         d_Lp(ri*sin_a, -ri*cos_a) + d_CACp(ri, laf, 0,       -ri, C.ccw) + 'Z';
}

// for use in "transform={rotate_deg(v)}"
export const rotate_deg = (v) => `rotate(` + vFix(v) + `)`;

export const getSecDeg  = (date) =>   date.getSeconds()*360/60;
export const getMinDeg  = (date) => ( date.getMinutes()*6 +      // (m+s/60)*360/60
                                      date.getSeconds()/10);
export const getHourDeg = (date) => ((date.getHours()%12)*30 +   // (h + m/60 + s/3600)*360/12
                                      date.getMinutes()/2    +
                                      date.getSeconds()/120);

export const rotate_sec  = (date) => rotate_deg(getSecDeg(date));
export const rotate_min  = (date) => rotate_deg(getMinDeg(date));
export const rotate_hour = (date) => rotate_deg(getHourDeg(date));

// overflow="visible" allows taking symbols drawn on the negative coordinates
// ES2018 property spread notation {...props}
export const SymbolV = props =>
  <symbol overflow="visible" {...props}>{props.children}</symbol>;

// circle at the center coord.
export const CircleC = props =>
  <circle cx="0" cy="0" {...props} />




