import React from 'react';
import {
  vFix,
  d_Mhx,
  d_Mvy
} from './svgutil.js';
import './space.css';

class Space extends React.Component {
  shouldComponentUpdate(nextProps) {
    return this.props.spec !== nextProps.spec;
  }

  render() {
    console.log(`Space:render(${this.props.spec.id})`);
    const c      = this.props.spec.ctr;
    const r_max  = this.props.spec.r_max;
    const sr_max = r_max - this.props.spec.bw_max;   // space circle radius

    const e_sz = Math.round(sr_max*.9);   // earth image for test
    const p_sz = Math.round(sr_max*.1);   // pikachu image for test
    const pSun = {
      x: vFix(c.x + e_sz*.6),
      y: vFix(c.y - e_sz/3)
    };

    return (
      <g id="space">
        <image xlinkHref="img/earth-test.jpg"
          x={c.x-e_sz/2} y={c.y-e_sz/2} width={e_sz} height={e_sz} filter="url(#blursun)" />
        <image xlinkHref="img/pikachu.svg"
          x={c.x-p_sz-e_sz/7} y={c.y-p_sz/2-e_sz/4} width={p_sz} height={p_sz} />kkgt
        <circle fill="#f9ec48" cx={pSun.x} cy={pSun.y} r={p_sz/3} filter="url(#blursun)" />

        <path d={d_Mhx(c.x-sr_max, c.y, sr_max*2) +
                 d_Mvy(c.x, c.y-sr_max, sr_max*2)}
          stroke="lightyellow" strokeWidth=".4" strokeDasharray="1,1" strokeOpacity=".8" />
      </g>
    );
  }
}

export default Space;

