import React from 'react';
import {
  vFix,
  d_Mp,
  d_Lp,
  d_MVy,
  d_DoubleMCAC,
  d_MaCircle,
  d_Donut,
  CircleC,
  rotate_sec,
  rotate_min,
  rotate_hour,
  getHourDeg
} from './svgutil.js';

class Time extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date()
    }
  }

  componentDidMount() {
    this.timerID = setInterval(() => this.tick(), 1000);
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  tick() {
    this.setState(prevState => ({date: new Date()}));
  }

  render() {
    const c      = this.props.spec.ctr;
    const sr_max = this.props.spec.r_max - this.props.spec.bw_max;   // space circle radius
    const opH    = ".7";
    //    const date_op = { weekday: 'short', year: 'numeric', month: 'short', day: 'numeric'}; // toLocalDateString
    let dateStr = this.state.date.toLocaleTimeString('en-US');
    const h = this.state.date.getHours();
    if ((h) && (h !== 12) && (h % 12 < 10)) {  // when 12:xx am, getHours() === 0
      dateStr = "0" + dateStr;
    }

    const em          = (n)  => Math.round(sr_max*.1*n);    // font size, basic unit
    const sradius     = (k)  => sr_max*k;                   // get the multiple of space circle radius
    const GetSymbolId = (id) => (this.props.spec.id + id);  // generate unique SVG symbol id
    const RefSymbolId = (id) => ("#" + GetSymbolId(id));

    // tlen: hand tail(shorter) length
    // hlen: hand head length
    // w:    hand head half-width
    // strW: stroke width
    // rh:   ratio of the radius of the hole to the head half-width (0 for no hole)
    // rt:   radius of the tale ring (also, add the center ring)
    const PathHandFrame = ({tlen, hlen, w, strW, rh=0, rt=0, ...p}) => {
      const tw = w/6;               // hand tail half width
      const yh = -hlen+tlen*1.5;    // hand head position: move from top (-hlen) to 1.5*tlen
      let d = d_Mp(-tw,tlen)  +
              d_Lp(-w, yh)    +
              d_Lp(0,  -hlen) +
              d_Lp(w,  yh)    +
              d_Lp(tw, tlen)  + `Z`;
      if (rh > 0) { // add a hole
        let r = w*rh;
        let y = yh;
        for (let i=0; i < 7; i++) {  // safe loop
          d += d_MaCircle(r, 0,y);
          y += r*2;  // move hole center closer to the coord. center
          r *= .88;  // ratio of the contracting radius
        }
      }
      if (rt > 0) { // add the tale rings
        d += d_MaCircle(rt, 0,tlen+rt);
      }
      return <path d={d} strokeWidth={vFix(strW)} fillRule="evenodd" {...p} />;
    }

    /*
    // light beam extending over the hands: triangular form
    const p_HandBeamT = (r, w) =>   // radius of the beam, half width of the beam
      <path d={d_MLp(0,0, -w,-r) + d_Hx(w) + `Z`} stroke="none" fill="url(#gradHandBeam)" />;
    */
    const PathHandLaser = ({r, w}) =>
      <path d={d_MVy(0,0, -r)} stroke="maroon" strokeWidth={w} />;

    // light beam with radius r and angle of 2*deg
    const PathHandBeam = ({r, deg}) =>
      <path d={d_DoubleMCAC(r, deg)} stroke="none" fill="url(#gradHandBeam)" />;

    const HourHandSymbol = () =>
      <symbol id={GetSymbolId("hh")} overflow="visible" stroke="DarkKhaki" fill="gold" opacity={opH}>
        <PathHandFrame tlen={sradius(.06)} hlen={sradius(.55)} w={em(.4)} strW={em(.04)} rh={.9}
          filter="url(#blursun)"/>
        <PathHandBeam  r={this.props.spec.r_b[3]} deg={1.5} />
        <PathHandLaser r={this.props.spec.r_b[0]} w={em(.02)} />
      </symbol>;

    const MinuteHandSymbol = () =>
      <symbol id={GetSymbolId("mh")} overflow="visible" stroke="DarkKhaki" fill="silver" opacity={opH}>
        <PathHandFrame tlen={sradius(.07)} hlen={sradius(.98)} w={em(.3)} strW={em(.04)} rh={.9}
          filter="url(#blursun)"/>
        <PathHandBeam  r={this.props.spec.r_b[4]} deg={1} />
        <PathHandLaser r={this.props.spec.r_b[0]} w={em(.02)} />
      </symbol>;

    const SecondHandSymbol = () =>
      <symbol id={GetSymbolId("sh")} overflow="visible" stroke="crimson" fill="none" opacity={opH}>
        <PathHandFrame tlen={sradius(.04)} hlen={sradius(1.02)} w={em(.1)} strW={em(.08)} rt={em(.15)} />
        <CircleC r={em(.1)} strokeWidth={em(.08)} fill="darkred" />
        <PathHandLaser r={this.props.spec.r_b[0]} w={em(.02)} />
      </symbol>;

    const SecondHand = () => <use xlinkHref={RefSymbolId("sh")} transform={rotate_sec(this.state.date)} />;
    const MinuteHand = () => <use xlinkHref={RefSymbolId("mh")} transform={rotate_min(this.state.date)} />;
    const HourHand   = () => <use xlinkHref={RefSymbolId("hh")} transform={rotate_hour(this.state.date)} />;

    const HourProgress = () =>
      <path d={d_Donut(this.props.spec.r_b[3], this.props.spec.r_b[4], getHourDeg(this.state.date))}
        fill="maroon" stroke="none" opacity=".2" />;

    return (
      <g id="clock" transform={`translate(${c.x},${c.y})`}>
        <g textAnchor="middle" fontSize={em(1.4)}>
          <text x="0" y={em(6)} fill="#555">88:88:88 88</text>
          <text x="0" y={em(6)} fill="lime">{dateStr}</text>
        </g>
        <text x="0" y={em(7)} textAnchor="middle" fill="lightgray" fontSize={em(1)}>
          {this.state.date.toDateString()}
        </text>
        <defs>
          <linearGradient id="gradHandBeam">
            <stop offset="0%"   stopColor="white"  stopOpacity="0" />
            <stop offset="50%"  stopColor="maroon" />
            <stop offset="100%" stopColor="white"  stopOpacity="0" />
          </linearGradient>
        </defs>
        <HourHandSymbol />
        <MinuteHandSymbol />
        <SecondHandSymbol />
        <HourProgress />
        <HourHand />
        <MinuteHand />
        <SecondHand />
      </g>
    );
  }
}

export default Time;



