// Soomin K.
// Tip.1: xlinkHref with <symbol> will not work if you put in the negative coordinates
// -> solution: overflow="visible"
// Tip.2: <symbol id> should be declared uniquely when there are multiple instances of the react element in a page.
// Can we have a globally (in a page) sharing <symbol> area that can cover any transform?
// Clock Face
import React from 'react';
import {
  vFix,
  d_MVy,
  d_MaCircle,
  d_MaUpperC,
  d_MaLowerC,
  getArcId,
  offsetFArcP,
  rotate_deg,
  d_Donut,
  d_MCAC,
  CircleC,
  SymbolV
} from './svgutil.js';
import Names from './names.json';

const IsLeapYear = (y =>
  ((y % 4   === 0) &&
   (y % 100 !== 0)) ||
   (y % 400 === 0));

// const DaysInMonth = (m, y) => new Date(y, m, 0).getDate();
// m: 1..12
const DaysInMonth = (m, y) => {
  switch (m) {
    case 4:
    case 6:
    case 9:
    case 11: return 30;
    case 2:  return IsLeapYear(y) ? 29 : 28;
    default: return 31;
  }
}

// year in the form like '67, '18
// const YearShort = (y) => ((y%100) < 10) ? "'0" + y%100 : "'" + y%100;

class CircularBands extends React.Component {
  shouldComponentUpdate(nextProps) {
    return this.props.spec !== nextProps.spec;
  }

  render() {
    console.log(`CircularBands:render(${this.props.spec.id})`);
    const fSet = {
      sans:   `Helvetica, "Trebuchet MS", Verdana, sans-serif`,
      serif:  `"Times New Roman", Times, serif`,
      mono:   `"Lucida Console", Monaco, monospace`
    };
    const r_b         = this.props.spec.r_b;
    const strW        = this.props.spec.r_max/500;
    const date        = new Date();
    const GetDaysYear = () => (IsLeapYear(date.getFullYear()) ? 366 : 365);
    const GetSymbolId = (id) => (this.props.spec.id + id);  // generate unique SVG symbol id
    const RefSymbolId = (id) => ("#" + GetSymbolId(id));
    const em          = (n) => Math.round(this.props.spec.r_max*.1*n);     // font size, basic unit

    // concentric bands, props
    const PathCircleBand = p =>
      <path id={GetSymbolId(`band${p.b}`)} d={d_MaCircle(r_b[p.b])} fill="none" />;
    // <circle key={k} id={GetSymbolId(`band${b}`)} cx="0" cy="0" r={vFix(r_b[b])} fill="none" />;
    // We cannot use the <circle> to write letters on the path.

    // separators
    const PathLineBand = p =>
      <path d={d_MVy(0,-r_b[p.b1], -r_b[p.b2])} transform={rotate_deg(p.deg)} />;

    // filled sectors
    // degOffset: starting deg
    const PathFillBand = p => p.degOffset ?
      <path d={d_Donut(r_b[p.b2], r_b[p.b1], p.deg)}
        fill={p.color} stroke="none" opacity={p.op || ".5"} transform={rotate_deg(p.degOffset)} /> :
      <path d={d_Donut(r_b[p.b2], r_b[p.b1], p.deg)}
        fill={p.color} stroke="none" opacity={p.op || ".5"} />;

    const Background = p =>
        <g id={GetSymbolId("bkg")} stroke="none">
          <CircleC r={vFix(r_b[p.b1])} fill={p.fillcolor1} />
          <CircleC r={vFix(r_b[p.b2])} fill={p.fillcolor2} />
        </g>;

    // pattern for optimization (to reduce the loop to 60/5=12)
    const YearBand = ({color, width, op="1"}) => {
      const Sym = "ybl";
      const d = d_MVy(0,-r_b[1], -r_b[0]);
      let lnsSym = [];
      for (let s=1; s <= 4; s++) {
        lnsSym.push(
          <path key={`ybsym${s}`} d={d} transform={rotate_deg(6*s)} />
        );
      }
      let lnsUse = [];
      for (let y=0; y < 12; y++) {
        lnsUse.push(
          <use key={10+y} xlinkHref={RefSymbolId(Sym)} transform={rotate_deg(y*360/12)} />
        );
      }
      return <g id={GetSymbolId("d-band")} stroke={color} strokeWidth={vFix(width)} strokeOpacity={op}>
          <SymbolV id={GetSymbolId(Sym)}>
            <path d={d} />
            {lnsSym}
          </SymbolV>
          <PathCircleBand b={0} />
          {lnsUse}
        </g>;
    }

    // For over 59 who entered the second lifecycle, emphasize the second cycle letters.
    const YearLabels = () => {
      const Sym   = ["yi", "yo"]; // symbol circle inner and outer
      const N     = 60;  // 60 years in full cycle
      const Bday  = new Date(this.props.spec.bdate);
      const yB    = Bday.getFullYear();
      const yN    = date.getFullYear() - Bday.getFullYear() + 1;  // total years, age
      const oldy  = (yN >= (N - 15));  // let's show the second N-years cycle for old enough people
      const overN = (yN > N);
      const bw    = this.props.spec.r_b[0] - this.props.spec.r_b[1];  // bandwidth
      const fz0   = em(.18);
      const fz    = (fz0 <= bw/1.7) ?
        [fz0,    fz0*.7] :  // fz[0]:active, fz[1]:inactive cycle font size
        [bw*.95, 0];        // when bandwidth is too small, print only the active cycle
      const rSym  = [
        this.props.spec.r_b[1]+fz[0]*.18,           // 1st, inner cycle
        this.props.spec.r_b[0]-fz[overN ? 0 : 1]    // 2nd, outer cycle
      ];
      const style = { // inline style
        active: {
          fontFamily:  fSet.mono,
          fontWeight:  "bold",
          fontSize:    fz[0],
          stroke:      "#444",
          strokeWidth: vFix(fz[0]/16),   // outline
          fill:        "brown",
        },
        inactive: {
          fontFamily:  fSet.mono,
          fontWeight:  "bold",
          fontSize:    vFix(fz[1]),   // smaller fontsize
          fill:        "black",
        },
        age: {
          fill:        "red",
          stroke:      "none"
        }
      };

      // The active cycle is determined by the age.
      const TextYearCycle = ({cycle}) => {
        const active = cycle ? overN : !overN;
        if (fz[1] <= 0) // no space to write the inactive cycle
          return null;
        let   lns = [];
        lns.push(  // conditional
          <defs key={`yrd${cycle}`}>
            <path id={GetSymbolId(Sym[cycle])} d={d_MaCircle(rSym[cycle])} />
          </defs>
        );
        let   y     = cycle ? N   : 0;
        const loopN = N + y;   // loopN = (1+cycle)*N
        while (y < loopN) {
          lns.push(
            <textPath key={`yr${y}`} xlinkHref={RefSymbolId(Sym[cycle])} startOffset={offsetFArcP(y,N)}>
              {`${yB+y}.`}
              <tspan style={style.age}>{y}</tspan>
            </textPath>
          );
          y++;
        }
        return <text textAnchor="middle" style={active ? style.active : style.inactive}>
          {lns}
        </text>;
      }
      
      return <g id={GetSymbolId("yearsCycle")}>
        <TextYearCycle cycle={0} />
        {oldy && <TextYearCycle cycle={1} />}
      </g>;
    }

    const MonthBand = ({color, width, op="1"}) => {
      const daysYear = GetDaysYear();
      const Sym = "mbl";
      let lnsUse = [];
      let a_days = 0;
      for (let m=1; m <= 12; m++) {
        lnsUse.push(
          <use key={100+m} xlinkHref={RefSymbolId(Sym)} transform={rotate_deg(a_days*360/daysYear)} />
        );
        a_days += DaysInMonth(m, date.getFullYear());  // m starts from 1
      }
      return <g id={GetSymbolId("m-band")} stroke={color} strokeWidth={vFix(width)} strokeOpacity={op}>
          <SymbolV id={GetSymbolId(Sym)}>
            <PathCircleBand b={1} />
            <path d={d_MVy(0,-r_b[3], -r_b[1])} />
          </SymbolV>
          {lnsUse}
        </g>;
    }

    const MonthNames = () => {
      const fz  = Math.min(this.props.spec.bw_max/10, em(.4));
      const Sym = ["MnNH", "MnSH"]; // Month names on the Northern or Southern Arc
      const N   = 12;  // total number of months in the full cycle
      const D   = 365.25/2;  // total (average) days in each hemi-Arc
      const oAP = [[  // offset in the Arc in percentage; offsetArcP(aId.ia, N) is enough for low accuracy
        50-100*(31+30+31/2)/D,      // oct
        50-100*(31+30/2)/D,         // nov
        50-100*31/2/D,              // dec
        50+100*31/2/D,              // jan
        50+100*(31+28.25/2)/D,      // feb
        50+100*(31+28.25+31/2)/D],  // mar
        [
        100*(31+30+31+30/2)/D-50,           // sep
        100*(31+30+31+30+31/2)/D-50,        // aug
        100*(31+30+31+30+31+31/2)/D-50,     // jul
        150-100*(31+28.25+31+30+31+30/2)/D, // jun
        150-100*(31+28.25+31+30+31/2)/D,    // may
        150-100*(31+28.25+31+30/2)/D]       // apr
        ];
      const style = {
        fontFamily:  fSet.sans,
        fontWeight:  "bold",
        fontSize:    fz,
        textAnchor:  "middle",
        stroke:      "gray",
        strokeWidth: vFix(fz/20),
        fill:        "teal"
      };
      const lnsTP = Names.Month.en.map((name, m) => { // name.substring(0,3)
        const aId = getArcId(m, N);
        return <textPath key={`mn${m}`} xlinkHref={RefSymbolId(Sym[aId.ul])}
          startOffset={vFix(oAP[aId.ul][aId.ia])+'%'}>{name}</textPath>;
      });

      return <g id={GetSymbolId("monthNames")}>
        <defs>
          <path id={GetSymbolId(Sym[0])} d={d_MaUpperC(this.props.spec.r_b[1]-fz)} />
          <path id={GetSymbolId(Sym[1])} d={d_MaLowerC(this.props.spec.r_b[1]*.985)} />
        </defs>
        <text style={style}>
          {lnsTP}
        </text>
      </g>;
    }

    // pattern of a week for optimization (to reduce the loop to approx. 365/7=52..)
    const DayBand = ({color, width, op="1"}) => {
      const daysYear = GetDaysYear();
      const degDay = (d) => d*360/daysYear;  // days to degree
      const diw = 7;    // days in a week
      const Sym = {
        line: "dbl",
        fill: "dbf",
        week: "dbw"
      };
      const d_line = d_MVy(0,-r_b[3], -r_b[2]);
      const d_fill = d_Donut(r_b[2], r_b[3], 1*360/daysYear);
      const fColor = {
        Sun: "red",
        Sat: "blue"
      }
      let lnsSym = [];
      for (let s=1; s <= 6; s++) {  // build a symbol of 7 days in a week
        lnsSym.push(
          <path key={`dbsym${s}`} d={d_line} transform={rotate_deg(degDay(s))} />
        );
      }

      let lnsUse = [];
      let dow = new Date(date.getFullYear(), 0, 1).getDay();  // sun:0 ~ sat:6
      let d   = 0;
      let deg = 0;
      let bWeekSymLoop = true;  // OK to use 7-days week symbol: enough days left to process
      while (d < daysYear) {
        if (bWeekSymLoop && (dow === 0)) {  // It's Sunday, start printing the weekly pattern.
          while (d <= (daysYear-diw)) {  // A year has 52 weeks. (+1 or 2 days)
            lnsUse.push(
              <use key={1500+d} xlinkHref={RefSymbolId(Sym.week)} transform={rotate_deg(degDay(d))} />
            );
            d += diw;
          }
          bWeekSymLoop = false;
        }
        deg = degDay(d);
        lnsUse.push(
          <use key={1000+d} xlinkHref={RefSymbolId(Sym.line)} transform={rotate_deg(deg)} />
        );
        if (dow === 0 || dow === 6) {
          lnsUse.push(
            <use key={1500+d} xlinkHref={RefSymbolId(Sym.fill)}
              fill={(dow === 0) ? fColor.Sun : fColor.Sat} transform={rotate_deg(deg)} />
          );
        }
        d++;
        dow = ++dow % diw;
      }
      return <g id={GetSymbolId("d-band")} stroke={color} strokeWidth={vFix(width)} strokeOpacity={op}>
          <SymbolV id={GetSymbolId(Sym.line)}>
            <path d={d_line} />
          </SymbolV>
          <SymbolV id={GetSymbolId(Sym.fill)}>
            <path d={d_fill} stroke="none" opacity={op} />
          </SymbolV>
          <SymbolV id={GetSymbolId(Sym.week)}>
            <path d={d_line} />
            <path d={d_fill} stroke="none" opacity={op} fill={fColor.Sun} />
            {lnsSym}
            <path d={d_fill} transform={rotate_deg(degDay(6))} stroke="none" opacity={op} fill={fColor.Sat} />
          </SymbolV>
          <PathCircleBand b={2} />
          {lnsUse}
        </g>;
    }

    const DateLabels = () => {
      const Sym = ["day1_28", "day29", "day30", "day31"];
      const maxD = 28;   // maximum day covered by the first <symbol>
      const bw = this.props.spec.r_b[2]-this.props.spec.r_b[3];
      const fz = Math.min(bw*.6, em(.12));
      const x0 = vFix((this.props.spec.r_b[2]+this.props.spec.r_b[3])/2);
      const thisYr = date.getFullYear();
      const daysYear = GetDaysYear();
      const degDay    = (d) => d*360/daysYear;            // deg of the n-th day (actual clock angle)
      const degSymDay = (s) => (degDay(s) - 89.2);        // deg of the n-th day in the symbol
      const rotate_s  = (s) => rotate_deg(degSymDay(s));  // rotate to the angle of n-th day in the symbol
      const rotate_d  = (d) => rotate_deg(degDay(d));     // rotate to the n-th day location

      let lnsText = [];
      for (let d=1; d <= maxD; d++) {  // 1~28 days texts to be used as one symbol
        lnsText.push(
          <text key={`txt${d}`} x={x0} y="0" transform={rotate_s(d-1)}>{d}</text>
        );
      }

      let lnsSym = [];
      for (let s=1; s < 4; s++) {  // build additional symbols from 29 to 31
        lnsSym.push(
          <SymbolV key={`sym${s}`} id={GetSymbolId(Sym[s])}>
            <text x={x0} y="0" transform={rotate_s(0)}>{maxD+s}</text>
          </SymbolV>
        );
      }

      let lnsUse = [];
      let n=0;  // n-th day
      for (let m=1; m <= 12; m++) {  // Using <symbols>s, write out each days for the whole year
        let dm = DaysInMonth(m, thisYr);
        lnsUse.push(
           <use key={`dl${n}`} xlinkHref={RefSymbolId(Sym[0])} transform={rotate_d(n)} />
        );
        n += maxD;  // covered by the <symbol> pattern
        let d=maxD; // to print the rest of days in each month (29~31)
        while (++d <= dm) {
          lnsUse.push(
            <use key={`dl${n}`} xlinkHref={RefSymbolId(Sym[d-maxD])} transform={rotate_d(n++)} />
          );
        }
      }

      // to reduce the loop count, use the monthly repeating pattern up to day 28th.
      return <g id={GetSymbolId("dateLabels")}
          fontFamily={fSet.mono} fontSize={vFix(fz)} fontWeight="bold" textAnchor="middle">
          <SymbolV id={GetSymbolId(Sym[0])}>
            {lnsText}
          </SymbolV>
          {lnsSym}
          {lnsUse}
        </g>;
    }

    const HourBand = ({color, width, op="1"}) => {
      let lns = [];
      for (let h=0; h < 12; h++) {
        lns.push(
          <PathLineBand key={2000+h} b1={5} b2={3} deg={h*360/12} />
        );
      }
      return <g id={GetSymbolId("h-band")} stroke={color} strokeWidth={vFix(width)} strokeOpacity={op}>
          <PathCircleBand b={3} />
          {lns}
        </g>;
    }

    const HourLabels = () => {
      const fz   = Math.min(this.props.spec.r_b[5]/6, em(.8));
      const N    = 12;   // 12 hours
      const Sym  = "hN";
      let lnsTP  = [];
      for (let h=0; h < N; h++) {
        lnsTP.push(   // shrink each hour except 3,6,9,12
          <textPath key={`hr${h}`} xlinkHref={RefSymbolId(Sym)}
            startOffset={offsetFArcP(h,N)}
            fontSize={vFix(fz*(1-(h%3 ? .15 : 0)))}
            fill={h%3 ? "#e5e4e2" : "khaki"}>
            {Names.Hour.roman[h]}
          </textPath>
        );
      }
      // dy={vFix(fz)}  does not work for firefox, only the first number 'XII' shows.
      // Technique! Use rotate(-15) to avoid the text overflow and hidden in the position of 100%
      return <g id={GetSymbolId("hourLabels")}>
        <defs>
          <path id={GetSymbolId(Sym)} d={d_MaCircle(this.props.spec.r_b[5]-fz)} transform="rotate(-15)" />
        </defs>
        <text fontFamily={fSet.serif} textAnchor="middle" stroke="purple" strokeWidth={vFix(fz*.01)}>
          {lnsTP}
        </text>
      </g>;
    }

    // use pattern for the optimization (to reduce the loop to 60/5=12)
    const BoundaryBand = ({color, width, op="1"}) => {
      const Sym = "minm";
      const cy = -(r_b[5]+r_b[4])/2; // center of the circle for each minute except 5x min
      const max_r = Math.abs(cy)*.05*.6;       // cy*sin(6 deg)/2 then circles will overlap
      const r  = Math.min(max_r, (r_b[4]-r_b[5])/4, 2.5);   // radius of the circle, max will be 2.5px
      const d  = d_MVy(0,-r_b[5], cy+r) + d_MaCircle(r, 0,cy) +  // vertical lines extended from a circle
                 d_MVy(0,cy-r,    -r_b[4]);
      const fColor = "gray";
      let lnsSym = [];
      for (let s=1; s <= 4; s++) {
        lnsSym.push(
          <path key={`pbsym${s}`} d={d} transform={rotate_deg(6*s)} />
        );
      }
      let lnsUse = [];
      for (let m=0; m<12; m++) {   // minute markers
        lnsUse.push(
          <use key={3010+m} xlinkHref={RefSymbolId(Sym)} transform={rotate_deg(m*360/12)} />
        );
      }
      return <g id={GetSymbolId("p-band")} stroke={color} strokeWidth={vFix(width)} strokeOpacity={op}>
          <SymbolV id={GetSymbolId(Sym)} stroke="black" fill={fColor}>
            {lnsSym}
          </SymbolV>
          <text textAnchor="middle" fontFamily={fSet.mono} stroke="none" fill="olive" fontSize={em(.05)}>
            <textPath xlinkHref={RefSymbolId(`band${5}`)} startOffset="25.1%">
              <tspan dy={em(-.03)}>(C) 2018 . dashgon.com</tspan>
            </textPath>
          </text>
          <PathCircleBand b={4} />
          <PathCircleBand b={5} />
          {lnsUse}
        </g>;
    }

    const SpaceTimeTextLogo = () => {
      const Sym = "textLogoPath";
      const fz  = Math.min(this.props.spec.r_b[5]/8, em(.5));
      return <g id={GetSymbolId("spacetimeLogo")}>
        <defs>
          <path id={GetSymbolId(Sym)} d={d_MaUpperC(this.props.spec.r_b[5] - fz*3)} />
        </defs>
        <text textAnchor="middle" fill="url(#Grad02)" fontSize={fz}>
          <textPath xlinkHref={RefSymbolId(Sym)} startOffset="50%">spacetime navigator</textPath>
        </text>
      </g>;
    }
    
    // p.sid : symbol id
    // p.r : radius
    // p.deg: total amount of degree
    // p.frDeg: starting deg
    // alignmentBaseline="middle" works only within <textPath> in Chrome & Safari
    const PathCBar = p =>
      <React.Fragment>
        <path id={GetSymbolId(p.sid)} d={d_MCAC(p.r, p.deg)} transform={rotate_deg(p.frdeg)} {...p} />
        <text textAnchor="middle" fill="white" fontFamily={fSet.mono} fontSize={em(.16)} fontWeight="bold">
          <textPath xlinkHref={RefSymbolId(p.sid)} alignmentBaseline="middle" startOffset="50%">
            {p.children}
          </textPath>
        </text>
      </React.Fragment>;
    
    const Demo = () => {
      const daysYear = GetDaysYear();
      const oneDay  = 24*60*60*1000; // hours*minutes*seconds*milliseconds
      const day0    = new Date(date.getFullYear(), 0, 1);  // Jan:0
      const days    = (date.getTime() - day0.getTime())/oneDay;
      const Bday    = new Date(this.props.spec.bdate);
      const age     = (date.getTime() - Bday.getTime())/(oneDay*365.2422);
      const Bday0   = new Date(Bday.getFullYear(), 0, 1);
      const baseR   = this.props.spec.r_b[1];
      const under60 = (age < 60);
      const degSt   = (Bday.getTime() - Bday0.getTime())/(oneDay*365.2422)*360/60;  // starting deg (offset)
      const degVal  = under60 ? age*360/60 : (age-60)*360/60;              // how much deg to draw
      const degDays = days*360/daysYear;
      const fillClr = ["ForestGreen", "Maroon"];

      return <g id="demo">
          <defs>
            <linearGradient id="Grad01">
              <stop offset="5%"   stopColor="#01E400"/>
              <stop offset="25%"  stopColor="#FEFF01"/>
              <stop offset="40%"  stopColor="#FF7E00"/>
              <stop offset="60%"  stopColor="#FB0300"/>
              <stop offset="80%"  stopColor="#9B004A"/>
              <stop offset="100%" stopColor="#7D0022"/>
            </linearGradient>
            <linearGradient id="Grad02">
                <stop offset="5%"  stopColor="gold"/>
                <stop offset="95%" stopColor="purple"/>
            </linearGradient>
          </defs>
          <PathFillBand b1={1} b2={0}
            degOffset={degSt} deg={degVal} color={under60 ? fillClr[0] : fillClr[1]} />
          {!under60 &&
          <PathFillBand b1={1} b2={0}
            degOffset={degSt+degVal} deg={360-degVal} color={fillClr[0]} op=".4" />}
          <PathFillBand b1={3} b2={1} deg={degDays} color="black" op=".4" />
          <g id="progress" fill="none "strokeWidth={em(.3)} strokeLinecap="round" opacity=".7">
            <path d={d_MCAC(baseR*.95, 180)} stroke="url(#Grad01)" transform="rotate(30)" />
            <path d={d_MCAC(baseR*.8,  100)} stroke="blue"         transform="rotate(100)" />
            <path d={d_MCAC(baseR*.78, 100)} stroke="gray"         transform="rotate(20)" />
            <path d={d_MCAC(baseR*.78,  90)} stroke="maroon"       transform="rotate(220)" />
            <path d={d_MCAC(baseR*.78,  40)} stroke="url(#Grad01)" transform="rotate(320)" />
            <PathCBar sid="a" r={this.props.spec.r_b[4]+em(.3)/2}
              stroke="url(#Grad01)" frdeg={180} deg={200} filter="url(#blursun)">Progress Test</PathCBar>
          </g>
        </g>;
    }

    return (
      <g id={GetSymbolId("clockface")}
        transform={`translate(${this.props.spec.ctr.x},${this.props.spec.ctr.y})`}>
        <Background b1={0} fillcolor1="white" b2={5} fillcolor2="black" />
        <YearBand      color="black"      width={strW}    op=".6" />
        <MonthBand     color="teal"       width={strW}    op=".8" />
        <DayBand       color="black"      width={strW}    op=".8" />
        <HourBand      color="black"      width={strW*2}  op=".8" />
        <BoundaryBand  color="dimgray"    width={strW/2}  op=".8" />
        <SpaceTimeTextLogo />
        <HourLabels />
        <YearLabels />
        <DateLabels />
        <MonthNames />
        <Demo />
      </g>
    );
  }
}

export default CircularBands;




